* {
  margin: 0;
  font-family: Titillium Web, sans-serif;
}

body, html, #root {
  width: 100%;
  height: 100%;
  background-color: #edfdf9;
  font-size: 62.5%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #b2dff2 !important;
}

button {
  color: #2a2a2a;
  cursor: pointer;
  background-color: #b4ccca;
  border: none;
  padding: 1.6rem 6.4rem;
  font-family: Titillium Web, sans-serif;
  font-size: 2.4rem;
}

.layout-wrapper {
  height: calc(100% - 6.4rem);
  flex-flow: row;
  display: flex;
  position: relative;
  top: 6.4rem;
}

.layout-wrapper .layout-container {
  width: 100%;
  height: 100%;
  width: auto;
  flex-flow: row;
  flex: 1;
  justify-content: center;
  align-items: flex-start;
  margin: 0;
  padding: 0 0 0 3.2rem;
  display: flex;
  position: relative;
}

.layout-wrapper .layout-container[tutorial="yes"]:before {
  content: " ";
  height: 40vh;
  opacity: .2;
  -webkit-filter: none;
  filter: none;
  width: 28%;
  background-image: url("../../assets/sequence.svg");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all 1s;
  position: absolute;
  top: 30vh;
  left: 15%;
}

.layout-wrapper .layout-container[tutorial="no"]:before {
  content: " ";
  height: 40vh;
  opacity: .2;
  -webkit-filter: none;
  filter: none;
  width: 28%;
  opacity: 0;
  -webkit-filter: blur(5px);
  filter: blur(5px);
  background-image: url("../../assets/sequence.svg");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all 1s;
  position: absolute;
  top: 30vh;
  left: 15%;
}

.layout-wrapper .layout-container[tutorial="yes"]:after {
  content: " ";
  height: 40vh;
  opacity: .2;
  -webkit-filter: none;
  filter: none;
  width: 35%;
  background-image: url("../../assets/parallel.svg");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all 1s;
  position: absolute;
  top: 30vh;
  right: 15%;
}

.layout-wrapper .layout-container[tutorial="no"]:after {
  content: " ";
  height: 40vh;
  opacity: .2;
  -webkit-filter: none;
  filter: none;
  width: 35%;
  opacity: 0;
  -webkit-filter: blur(5px);
  filter: blur(5px);
  background-image: url("../../assets/parallel.svg");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all 1s;
  position: absolute;
  top: 30vh;
  right: 15%;
}

.layout-wrapper .empty-container {
  flex-shrink: 0;
  flex-basis: 52.8rem;
  transition: flex-basis .2s ease-out;
}

.layout-wrapper .empty-container.expanded-empty {
  flex-basis: calc(6.4rem + 50%);
  transition: flex-basis .2s ease-out;
}

.layout-wrapper:before {
  content: "";
  height: 7.6rem;
  min-width: 100%;
  background-color: #b4ccca;
  position: fixed;
  top: 6.4rem;
  left: 0;
}

.column-container {
  min-height: calc(100% - 6.4rem);
  width: 40rem;
  background-color: rgba(220, 239, 234, .25);
  border-radius: 2.4rem;
  flex-flow: column;
  flex: 0 0 40rem;
  justify-content: flex-start;
  align-items: center;
  margin-top: 3.2rem;
  margin-right: 3.2rem;
  padding: 0 3.2rem;
  display: flex;
  position: relative;
}

.column-container .drag-handle {
  color: #8fa9ac;
  padding: 1.6rem;
  font-size: 2.4rem;
  line-height: 2.4rem;
  scale: 3 1;
}

.column-container .drag-handle:hover, .column-container .drag-handle:focus {
  color: #4c6773;
}

.column-container .hidden {
  display: none;
}

.column-container .delete-column {
  color: #8fa9ac;
  cursor: pointer;
  font-size: 2.4rem;
  position: absolute;
  top: 1.6rem;
  right: 1.6rem;
}

.column-container .delete-column:hover, .column-container .delete-column:focus {
  color: #4c6773;
}

.column-container:not(:last-of-type) > .add-column {
  opacity: 0;
}

.column-container:last-of-type > .add-column {
  color: #8fa9ac;
  cursor: pointer;
  font-size: 2.4rem;
  transition: opacity .2s ease-in-out;
  position: absolute;
  top: 1.6rem;
  right: -3.2rem;
}

.column-container:last-of-type > .add-column:hover, .column-container:last-of-type > .add-column:focus {
  color: #4c6773;
}

.column-container .tasks-wrapper {
  width: 100%;
  flex-flow: column;
  flex: 1;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.task-wrapper {
  width: 40rem;
  min-height: 19.2rem;
  border-radius: 2.4rem;
  margin-bottom: 3.2rem;
  overflow: hidden;
}

.menu-container {
  width: 0;
  height: calc(100% - 6.4rem);
  min-width: 46.4rem;
  z-index: 2;
  background-color: #4c6773;
  border-radius: 2.4rem;
  transition: width .2s ease-out;
  position: fixed;
  top: 3.2rem;
  right: 3.2rem;
  box-shadow: 1px 10px 20px rgba(0, 0, 0, .2);
}

.menu-container .tabs {
  height: 100%;
}

.menu-container .tabs .tab-list {
  width: calc(100% - 6.4rem);
  height: 3.2rem;
  background-color: rgba(47, 72, 88, .25);
  border-radius: 2.4rem;
  justify-content: center;
  align-items: center;
  margin: 3.2rem;
  padding: 0;
  display: flex;
  overflow: hidden;
}

.menu-container .tabs .tab-list button {
  width: 30%;
  height: 100%;
  color: #e0e0e0;
  background-color: rgba(0, 0, 0, 0);
  flex: 1;
  padding: 0;
  font-size: 1.8rem;
}

.menu-container .tabs .tab-list button:focus, .menu-container .tabs .tab-list button:hover {
  background-color: rgba(47, 72, 88, .25);
}

.menu-container .tabs .tab-list button.active-tab {
  color: #2a2a2a;
  background-color: #b4ccca;
}

.menu-container .tabs .hidden {
  display: none;
}

.menu-container .tabs .active-panel {
  width: 100%;
  height: 100%;
}

.toggle-size {
  width: 5.6rem;
  transition: -webkit-transform .2s linear, transform .2s linear;
  position: absolute;
  top: calc(50% - 1.2rem);
  left: -2.8rem;
}

.toggle-size .icon {
  scale: 1.2;
}

.toggle-size.expand {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.toggle-size.expand .icon {
  color: #8fa9ac;
  font-size: 2.4rem;
}

.toggle-size.expand .icon:hover, .toggle-size.expand .icon:focus {
  color: #4c6773;
}

.toggle-size.collapse {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.toggle-size.collapse .icon {
  color: #8fa9ac;
  font-size: 2.4rem;
}

.toggle-size.collapse .icon:hover, .toggle-size.collapse .icon:focus {
  color: #dcefea;
}

.expanded-menu {
  width: 50%;
  transition: width .2s ease-out;
}

.themed-input {
  height: 1.8rem;
  text-align: right;
  background-color: #fff;
  border: 1px solid #4c6773;
  font-family: Roboto Mono, monospace;
  font-size: 1.4rem;
}

.themed-textarea {
  height: 200px;
  text-align: left;
  background-color: #fff;
  border: 1px solid #4c6773;
  font-family: Roboto Mono, monospace;
  font-size: 1.4rem;
}

.spacer {
  flex: 1;
}

.tab-panel {
  height: calc(100% - 9.6rem);
  flex-flow: column;
  justify-content: center;
  align-items: center;
  gap: 3.2rem;
  display: flex;
}

.tab-panel > div {
  height: calc(100% - 3.2rem);
  width: calc(100% - 6.4rem);
  flex-flow: column;
  padding: 0 3.2rem 3.2rem;
  display: flex;
}

.loader {
  width: 80px;
  height: 80px;
  display: inline-block;
}

.loader:after {
  content: " ";
  width: 64px;
  height: 64px;
  border: 6px solid #8fa9ac;
  border-color: #8fa9ac rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 8px;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
  animation: lds-dual-ring 1.2s linear infinite;
  display: block;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

